import React, { useEffect } from 'react';
import Home from './pages/Home';
import Project from './pages/Project';
import PostDetailPage from './pages/post-detail';
import usePageStore, { ViewType } from './store/page.store';

import { QueryClient, QueryClientProvider } from 'react-query';

const App: React.FC = () => {
  const queryClient = new QueryClient();

  const page = usePageStore((state) => state);

  const routes: any = {
    [ViewType.INDEX]: Home,
    [ViewType.PROJECT]: Project,
    [ViewType.POKEMON]: '',
    [ViewType.POST_DETAIL]: PostDetailPage,
  };

  function renderComponent() {
    const Component = routes[page.viewType.name];

    if (!Component) {
      page.setView({ name: ViewType.INDEX });
    }

    return Component ? <Component /> : null;
  }

  // function initiateTawkTo() {
  //   const script = document.createElement('script')
  //   script.src = 'https://embed.tawk.to/618493476885f60a50ba64af/1fjmtuknp'
  //   script.async = true

  //   window.document.body.append(script)
  // }

  // useEffect(() => {
  //   initiateTawkTo();
  // }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [page.viewType]);

  useEffect(() => {
    page.setView({ name: ViewType.INDEX });
  }, []);

  return (
    <QueryClientProvider client={queryClient}>
      <div>{renderComponent()}</div>
    </QueryClientProvider>
  );
};

export default App;
