import create from 'zustand';
import { persist, devtools } from 'zustand/middleware';
import { TObject } from '../types';

export enum ViewType {
  INDEX = 'INDEX',
  PROJECT = 'PROJECT',
  POKEMON = 'POKEMON',
  POST_DETAIL = 'POST_DETAIL',
}

type IState = {
  viewType: {
    name: string;
    params?: TObject;
  };
  setView: (value: { name: string; params?: TObject }) => void;
};

const usePageStore = create<IState>()(
  devtools(
    persist(
      (set, get) => ({
        viewType: {
          name: ViewType.INDEX,
        },
        setView(value: { name: string; params?: TObject }) {
          set({
            viewType: value,
          });
        },
      }),
      {
        name: 'page.store',
      }
    )
  )
);

export default usePageStore;
