import React from 'react';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

type ISocial = {
  label: string;
  icon: any;
  url: string;
};

type IContact = {
  email: string;
  socials: ISocial[];
};

const Links: React.FC = () => {
  const contact: IContact = {
    email: 'rudestewing@gmail.com',
    socials: [
      {
        label: 'Github',
        icon: GitHubIcon,
        url: 'http://github.com/rudestewing',
      },
      {
        label: 'Linkedin',
        icon: LinkedInIcon,
        url: 'https://www.linkedin.com/in/rudi-setiawan-a2820217a/',
      },
    ],
  };

  return (
    <div>
      <div className="my-5 border border-b border-gray-300"></div>
      <div className="flex justify-center flex-wrap gap-3 items-center">
        {contact.socials.map((item, index) => {
          return (
            <a
              key={index}
              href={item.url}
              target="_blank"
              rel="noreferrer"
              className="hover:text-gray-800 hover:no-underline flex mb-3 rounded-lg bg-white shadow-md py-3 pl-8 pr-10 items-center"
            >
              <item.icon />
              <div className="pl-3">{item.label}</div>
            </a>
          );
        })}
      </div>
      <div className="flex flex-col items-center">
        <div className="flex mb-3">
          <div>
            <a href={`mailto:${contact.email}`} className="underline text-blue-700">
              {contact.email}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Links;
