import React from 'react';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import GitHubIcon from '@mui/icons-material/GitHub';
import usePageStore, { ViewType } from '../store/page.store';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
type IProps = {
  children: any;
  hasBack?: boolean;
  onBack?: () => void;
};

type INav = {
  icon: any;
  viewType: string;
  label: string;
};

const Layout: React.FC<IProps> = ({ children, hasBack, onBack }) => {
  const page = usePageStore((state) => state);

  const navs: INav[] = [
    {
      viewType: ViewType.INDEX,
      icon: AccountCircleIcon,
      label: 'Profile',
    },
    {
      viewType: ViewType.PROJECT,
      icon: GitHubIcon,
      label: 'Project & Research',
    },
  ];

  const handleBack = () => {
    if (onBack) {
      onBack();
    }
  };

  return (
    <div className="bg-gray-200 min-h-screen">
      <div className="bg-gray-200 min-h-screen max-w-screen-sm mx-auto text-gray-900 text-sm pb-16">
        <div
          className="fixed h-[60px] bg-white bottom-0 left-0 right-0 z-30"
          style={{
            bottom: 0,
            left: 0,
            right: 0,
            boxShadow: '-2px 0 4px 2px #d3d3d3',
          }}
        >
          <div className="w-full h-full max-w-screen-sm mx-auto px-3 flex gap-2 justify-end items-center">
            {/*  <div>
              <Dropdown
                trigger={['click']}
                placement="bottomRight"
                overlay={
                  <Menu
                    items={navs.map((item) => {
                      return {
                        key: item.label,
                        label: item.label,
                        onClick: () => {
                          page.setView(item.viewType);
                        },
                      };
                    })}
                  />
                }
              >
                <span className="p-3 flex justify-center items-center cursor-pointer">
                  <MenuIcon />
                </span>
              </Dropdown>
            </div> */}
            <ul className="flex h-full items-center lg:justify-center gap-3 w-full">
              {navs.map((item, index) => {
                return (
                  <li
                    v-for="(route, index) in routes"
                    key={index}
                    className="flex items-center justify-center w-1/2 lg:w-auto"
                  >
                    <span
                      onClick={() => page.setView({ name: item.viewType })}
                      className={`
                      ${
                        page.viewType.name === item.viewType
                          ? 'bg-indigo-500 text-gray-100'
                          : 'bg-white text-gray-900'
                      }
                      flex items-center justify-between gap-2 h-12 w-full px-5 cursor-pointer text-3xl hover:bg-indigo-500 hover:text-gray-100 no-underline hover:no-underline shadow-md outline-none rounded-lg border border-blue-300 transition-all
                    `}
                    >
                      <span className="text-xs md:text-sm">{item.label}</span>
                      <item.icon style={{ fontSize: 26 }} />
                    </span>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
        <div className="z-10">
          {hasBack && (
            <div className="fixed top-0 left-0 right-0 max-w-screen-sm py-2 px-1 flex items-center justify-start mx-auto">
              <div
                className="px-3 py-2 flex items-center justify-center bg-white rounded-lg cursor-pointer hover:bg-indigo-500 hover:text-white"
                onClick={handleBack}
              >
                <ArrowBackIosIcon
                  fontSize="small"
                  style={{ fill: 'currentcolor' }}
                />
              </div>
            </div>
          )}
          <div className="h-12 container mx-auto flex justify-center items-center">
            <MoreHorizIcon fontSize="medium" color="inherit" />
          </div>
          {children}
        </div>
      </div>
    </div>
  );
};

export default Layout;
